import { mapState } from "vuex";
import { ElNotification } from "element-plus";
export default {
  name: "ResultShow",
  data() {
    return {};
  },
  computed: {
    ...mapState(["currentShow"])
  },
  methods: {
    getModelType(model_type) {
      switch (model_type) {
        case 0:
          {
            return "酶靶标预测";
          }
        case 1:
          {
            return "离子通道靶标预测";
          }
        case 2:
          {
            return "G蛋白偶联受体靶标预测";
          }
        case 3:
          {
            return "核受体靶标预测";
          }
        default:
          return "个人数据预测";
      }
    },
    getEstimatorType(estimatorType) {
      switch (estimatorType) {
        case 0:
          {
            return "KNN算法";
          }
        case 1:
          {
            return "朴素贝叶斯";
          }
        case 2:
          {
            return "决策树";
          }
        case 3:
          {
            return "决策森林";
          }
        default:
          return "数据出错";
      }
    },
    deleteModel() {
      let params = {
        model_type: this.currentShow.model_type,
        file_name: this.currentShow['model_name'] + '-' + this.currentShow['id']
      };
      window.pyqt5.predict.main_predict(JSON.stringify(params)).then(data => {
        if (data) {
          ElNotification({
            title: '模型删除',
            message: '模型数据已成功删除',
            duration: 0,
            type: 'success'
          });
        } else {
          ElNotification({
            title: '模型删除',
            message: '模型数据删除失败',
            duration: 0,
            type: 'error'
          });
        }
      });
    }
  }
};