import InfoShow from "@/components/ModelListShow.vue";
import { mapMutations, mapState } from "vuex";
import ResultShow from "@/components/ResultShow";
export default {
  name: "HomeComponent",
  components: {
    ResultShow,
    InfoShow,
    // eslint-disable-next-line vue/no-unused-components
    SIid: 0
  },
  unmounted() {
    clearInterval(this.SIid);
  },
  mounted() {
    this.flushModelInfoList();
    this.SIid = setInterval(function () {
      window.store.commit("flushModelInfoList");
    }, 4000);
  },
  computed: {
    _drawer: {
      get() {
        return this.drawer;
      },
      set(value) {
        this.setDrawer(value);
      }
    },
    ...mapState(["modelInfoList", "drawer"])
  },
  methods: {
    ...mapMutations(["setDrawer", "flushModelInfoList"])
  }
};